import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import store from '@/store/store.js'
//1. 按需导入 $http 对象  导入网络请求第三方的包
import { $http } from '@escook/request-miniprogram'

 
//2. 在 uni-app 项目中，可以把 $http 挂载到 uni 顶级对象之上，方便全局调用
uni.$http = $http

//3.请求拦截器 请求开始之前做一些事情
$http.beforeRequest = function (options) {
  uni.showLoading({
  	title:'数据加载中。。。'
  })
} 

//4.响应拦截器 请求完成之后做一些事情
$http.afterRequest = function () {
  uni.hideLoading()
}

//5. 配置轮播图请求的根路径
$http.baseUrl = 'https://www.uinav.com'

//6.封装弹窗方法
uni.showMsg = function(title = '数据请求失败!', duration = 2000){
	uni.showToast({
		title,
		duration,
		icon:'none'
	})
}

import Vue from 'vue';

Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App,
	store
})
app.$mount()